body {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: 0;
}

.image-container {
    position: relative;
    width: 600px;
    height: 400px;
}

.image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    pointer-events: none;
}

.image1 {
    clip-path: polygon(0 0, var(--slider-position, 50%) 0, var(--slider-position, 50%) 100%, 0 100%);
}

.image2 {
    clip-path: polygon(var(--slider-position, 50%) 0, 100% 0, 100% 100%, var(--slider-position, 50%) 100%);
}

.slider {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 50%;
    width: 7.42px;
    background-color: rgba(131, 131, 131, 0.32);
    border: 0.39053px solid rgba(255, 255, 255, 0.32);
    backdrop-filter: blur(3.12424px);
    box-shadow: 0px 0px 6.24847px #6100FF, inset -0.39053px -0.781059px 1.56212px rgba(0, 0, 0, 0.25), inset 0.39053px 0.781059px 4.68636px rgba(255, 255, 255, 0.64);
    cursor: col-resize;
}
